@use "./abstracts/mixins" as *;
@use "../base/colors" as *;

.about {
	background-color: #131618;

	&__row {
		display: flex;
		gap: 4em;
		flex-direction: column;
		&-img {
			width: 100%;
			height: auto;
			& img {
				height: auto;
				width: 100%;
				object-fit: contain;
				max-height: 600px;
			}
		}
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		gap: 1em;
		text-align: center;
		align-items: center;
		width: 100%;
		&-heading {
			font-size: clamp(1.6rem, 1.5517rem + 0.2759vw, 1.8rem);
			color: rgba(237, 243, 255, 0.4);
			font-weight: 300;
		}
		&-text {
			font-size: clamp(1.7rem, 1.3862rem + 1.7931vw, 3rem);
			color: #fff;
			font-weight: 600;
			&--span {
				color: #ffb800;
			}
		}
		&-subtext {
			font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
			color: #fff;
			font-weight: 200;
			overflow: hidden;
			// white-space: nowrap;
			max-width: 100%;
			max-height: 130px;
            transition: .6s ease-in-out;
			&.active {
				max-height: 1000px;
			}
			&-expand {
				font-size: clamp(1.7rem, 1.5rem + 0.5556vw, 2rem);
				color: #ffb800;
				border-radius: 14px;
				background-color: #1a2024;
				margin-top: 2vh;
				width: max-content;
				border: none;
				outline: none;
				padding: 0.6em 1.2em;
				cursor: pointer;
			}
			.expanded {
				white-space: normal;
				text-overflow: unset;
			}
		}
	}
}
